}

.feed__card {
  max-width: 600px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 50px;
  border-radius: 5px;
  background-color: white;
  position: relative;
}

.modal .feed__card {
  margin-bottom: 0;
}

.card__close {
  font-size: 25px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: 100;
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 20px;
  margin-top: 15px;
  cursor: pointer;
  display: none;
}

.modal .card__close {
  display: block;
}

.card__clip {
  position: absolute;
  width: 27px;
  top: -35px;
  right: 50px;
  cursor: pointer;
  opacity: 0.3;
  /* transition: transform 0.1s ease-in-out; */
}

.card__clip--liked {
  opacity: 1;
  /* transform: rotate(20deg);
  top: -32px;
  transition: transform 0.1s ease-in-out; */
}

.modal .card__clip {
  right: 80px;
}

.card__header {
  height: 60px;
  padding: 15px 25px;
  display: flex;
  align-items: center;
}

.card__header-img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  margin-right: 10px;
  border: 0.1px solid rgba(0, 0, 0, 0.1);
}

.card__img {
  width: 100%;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.card__footer {
  min-height: 85px;
  padding: 25px 25px;
}

.footer__movie {
  font-size: 15px;
  color: rgba(0, 0, 0, 0.5);
}

.footer__message {
  margin-top: 15px;
}

@media (max-width: 1000px) {
  .feed__card {
    margin: 0 auto;
    margin-bottom: 50px;
  }
}

@media (min-width: 640px) {
  .card__clip:hover {
    opacity: 1;
  }
}

@media (max-width: 640px) {
  section.feed {
    padding-top: 0;
  }
  .feed__card {
    border-left: none;
    border-right: none;
    border-radius: 0;
  }
  .card__img {
    border: none;
  }
  .card__clip {
    right: 50px;
  }
  .modal .card__clip {
    right: 60px;
  }
}

