}

.auth__divider::after {
  content: "";
  width: 40%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 8px;
  right: 1px;
}

.auth__divider::before {
  content: "";
  width: 40%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 8px;
  left: 1px;
}

