.input {
  display: block;
  width: 100%;
  border-radius: 5px;
  padding: 5px 10px;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: rgba(0, 0, 0, 0.01);
  font-size: 14px;
}

.input:focus {
  outline: none;
  box-shadow: 0 0 0 0.1pt rgba(0, 0, 0, 0.5);
}

.input:hover {
  box-shadow: 0 0 0 0.1pt rgba(0, 0, 0, 0.5);
}

.submit {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #4dabf7;
  border: 1px solid #4dabf7;
  border-radius: 4px;
  color: #fff;
