.submit:hover {
  background-color: #339af0;
  border: 1px solid #339af0;
}

.submit.submit--naver {
  max-width: 250px;
  margin: 0 auto;
  background-color: #00c640;
  border: 1px solid #00c640;
}

.submit.submit--naver:hover {
  background-color: #00af37;
  border: 1px solid #00af37;
}

.modal {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  overflow-y: auto;
  display: block;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.2s ease-in-out;
}

.modal--none {
  opacity: 0;
  pointer-events: none;
  transition: all 0.2s ease-in-out;
}

.modal__content {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 10%;
  z-index: 300;
  display: block;
  margin-bottom: 50px;
}

.modal__content--none {
  display: none;
}

.modal__content .feed__card {
  border: none;
}

@media (max-width: 640px) {
  .modal__content {
    width: 90%;
  }
  .modal__content > div {
    width: 100%;
  }
}

.s-alert-success {
